@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400');
@import url('https://fonts.googleapis.com/earlyaccess/notosanskr.css');
@import url('./lib/ManiaFrameWork/css/mania.css');

html, body {
  font-family: 'Noto Sans KR', 'Roboto';

}

.Logo {
  background-image: url('./media/images/logo1.png');
  background-size: 40px 40px;
  background-repeat: no-repeat;
  background-position: 20px center;
  padding-left: 70px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: -0.5px;
  font-family: 'Roboto';
  color: #000;
}

.codeArea {
  padding: 10px;
}

.code-highlight pre code {
  font-size: 13px;
}

.MF_area {
  font-weight: 400;
  color: #000;
  padding: 10px;
  overflow: hidden;

  h3 {
    color: #444;
  }

  .box {

  }
}

.colors {
  li {
    display: inline-block;
    text-align: center;
    width: 70px;
    color: #444;
    font-size: 12px;

    span {
      display: block;
      width: 40px;
      height: 40px;
      margin: 0 auto;
    }
  }
}
